import data from './adcode.json';

interface Adcode {
  parent: string | null;
  children: string[];
  name: string;
}

interface Adcodes {
  [key: string]: Adcode;
}

const adcodes: Adcodes = data

export default adcodes;

export interface AdcodeMeta {
  adcode: string;
  name: string;
  full_name: string;
}

export const get_full_name = (adcode: string) => {
  let name = adcodes[adcode].name
  let parent = adcodes[adcode].parent
  while (parent !== null && parent !== '100000') {
    name = adcodes[parent].name + " - " + name
    parent = adcodes[parent].parent
  }
  return name
}

export const get_adcode_meta = (adcode: string) => {
  const full_name = get_full_name(adcode)
  const name = adcodes[adcode].name
  const meta: AdcodeMeta = { adcode, name, full_name };
  return meta;
}

export const add_adcode = (adcode: string, excludes: string[] = []) => {
  let ret: string[] = []
  if (excludes.includes(adcode))
    return ret;

  if (adcode === '100000') {
    for (let sub_adcode of adcodes[adcode].children) {
      ret = ret.concat(add_adcode(sub_adcode, excludes))
    }
  }
  //    
  else if (['110000', '120000', '310000', '500000'].includes(adcode)) {
    ret.push(adcode)
  }
  // 
  else if (['710000', '810000', '820000', '830000'].includes(adcode)) {
    ret.push(adcode)
  }
  // 
  else if (adcode.slice(2, 6) === '0000') {
    for (let sub_adcode of adcodes[adcode].children) {
      ret = ret.concat(add_adcode(sub_adcode, excludes))
    }
  }
  else {
    ret.push(adcode)
  }
  return ret
}

export function shuffle(a: any[]) {
  let ret = a.slice()
  for (let i = ret.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [ret[i], ret[j]] = [ret[j], ret[i]];
  }
  return ret;
}
