import React from 'react';
import { useState } from 'react';
import { Button, Space, Card, List, CheckList } from 'antd-mobile';
import { Toast, FloatingPanel, NavBar, Divider, SafeArea } from 'antd-mobile';
import adcodes, { AdcodeMeta, add_adcode, shuffle, get_adcode_meta } from './Adcode';

import {
  PayCircleFilled,
  PayCircleOutlined,
  ClockCircleFilled,
  ClockCircleOutlined,
  SettingOutlined,
  SwapRightOutlined
} from '@ant-design/icons';

import './CardGame.css'

function getRandomInt(a: number, b: number) {
  return Math.floor(Math.random() * (b - a) + a);
}

interface CardProps {
  adcode: string;
  name: string;
  from: AdcodeMeta;
  money: number;
  time: number;
}

function calculateMoney(from: string, to: string) {
  return getRandomInt(1, 10) * 100;
}

function calculateTime(from: string, to: string) {
  return getRandomInt(1, 6);
}

function get_card(meta: AdcodeMeta, from_adcode: string): CardProps {
  const from = get_adcode_meta(from_adcode)
  const card: CardProps = {
    ...meta,
    from,
    money: calculateMoney(from_adcode, meta.adcode),
    time: calculateTime(from_adcode, meta.adcode)
  }
  return card;
}

const LocationCard: React.FC<{ card: CardProps, onClick: any }> = ({ card, onClick }) => {
  return (
    <CheckList.Item key={card.adcode} value={card.adcode} onClick={onClick} style={{ textAlign: 'left' }}>
      <h4>{card.name}</h4>
      <Space>
        {card.from.name}
        <SwapRightOutlined />
        <PayCircleOutlined />{card.money}
        <ClockCircleOutlined />{card.time}
      </Space>
    </CheckList.Item>
  );
}

const SelectStartScene: React.FC<{ setVisited: (visited: string[]) => void, setGameState: (state: number) => void }> = (props) => {
  const [candids, setCandids] = useState(shuffle(add_adcode("100000")).slice(0, 4));
  const [selected, setSelected] = useState('');

  const onConfirm = () => {
    props.setVisited([selected]);
    props.setGameState(1);
  }

  return (
    <>
      <h1></h1>
      <CheckList onChange={vals => {
        if (vals.length == 0)
          setSelected('')
        else
          setSelected(vals[0])
      }}>
        {candids.map(get_adcode_meta)
          .map(card =>
            <CheckList.Item key={card.adcode} value={card.adcode}>
              {card.name}
            </CheckList.Item>)}
      </CheckList>
      <div className='footer'>
        <Button block color='primary' size='large' disabled={selected == ''} onClick={onConfirm}>
          </Button>
      </div>
    </>
  )
}

const CardGame: React.FC = () => {
  // Game state
  // 0: select starting position
  // 1: main
  const [game_state, setGameState] = useState(0);
  const [money, setMoney] = useState(0);
  const [time, setTime] = useState(0);
  const [candids, setCandids] = useState<string[]>([]);
  const [visited, setVisited] = useState<string[]>([]);

  const setGameStateAndConfig = (state: number) => {
    if (state == 1) {
      setMoney(10000);
      setTime(36);
    }
    setGameState(state);
  }

  const updateCandids = (to_replace: string[] = []) => {
    let n = 3 - candids.length;
    if (n == 0 && to_replace.length == 0)  // No need to add or replace
      return;

    const excludes = visited.concat(candids).concat(to_replace);

    let all_candids = shuffle(add_adcode('100000', excludes));
    let curr = 0;

    for (let i = 0; i < candids.length; i++) {
      if (to_replace.includes(candids[i])) {
        candids[i] = all_candids[curr];
        curr++;
      }
    }

    let new_candids = all_candids.slice(curr, curr + n);
    setCandids(candids.concat(new_candids))
  }

  const onClickCard = (e: React.MouseEvent, adcode: string) => {
    let card = get_card(get_adcode_meta(adcode), visited[0])

    updateCandids([adcode]);
    visited.unshift(adcode);
    setVisited(visited);

    setTime(time - card.time);
    setMoney(money - card.money);

    Toast.show({
      content: `${card.from.name}  ${card.name}`,
      icon: <SettingOutlined spin style={{ fontSize: 24 }} />,
      duration: 500
    })
  }

  if (candids.length == 0) {
    updateCandids();
  }

  if (game_state == 0)
    return (
      <SelectStartScene setVisited={setVisited} setGameState={setGameStateAndConfig} />
    )
  else
    return (
      <div>
        <SafeArea position='top' />
        <h3>
          <Space>
            <PayCircleFilled />{`: ${money}`}
            <ClockCircleFilled />{`: ${time}`}
          </Space>
        </h3>
        <CheckList>
          {candids.map(get_adcode_meta)
            .map(meta => get_card(meta, visited[0]))
            .map(card => <LocationCard card={card} onClick={(e: React.MouseEvent) => onClickCard(e, card.adcode)} />)}
        </CheckList>
        <FloatingPanel anchors={[80, window.innerHeight * 0.4, window.innerHeight * 0.8]}>
          <List>
            {visited.map(get_adcode_meta)
              .map(card => (
                <List.Item key={card.adcode}>{card.name}</List.Item>
              ))}
          </List>
        </FloatingPanel>
        <SafeArea position='bottom' />
      </div>
    )
};

export default CardGame;
